import React from 'react';
import { Box, Grid, styled, Typography, useTheme } from '@mui/material';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { useProfileInfo } from 'hooks/useProfileInfo';
import MySocials from 'components/MySocials';
import { useTranslation } from 'i18n/useTranslation';

interface Props {
  onClickExternalLink?: (name: string) => void;
}
const IntroSection = ({ onClickExternalLink }: Props) => {
  const { spacing, breakpoints, typography } = useTheme();
  const { avatar } = useProfileInfo();
  const avatarImage = avatar != null ? getImage(avatar.childImageSharp) : null;
  const t = useTranslation();

  return (
    <Box component="section" sx={{ marginTop: spacing(6) }}>
      <Grid container alignItems="flex-end" sx={{ marginBottom: spacing(6) }}>
        <Grid item xs={12} lg>
          <Typography
            variant="h2"
            component="h1"
            sx={{
              fontFamily: '"Pretendard Variable", Pretendard, sans-serif',
              fontSize: typography.h3.fontSize,
              [breakpoints.down('md')]: {
                fontSize: typography.h4.fontSize,
              },
            }}
          >
            {t.aboutIntroTitle1}
            <br />
            <span dangerouslySetInnerHTML={{ __html: t.aboutIntroTitle2 }} />
          </Typography>
        </Grid>
        {avatarImage != null ? <ProfileImage image={avatarImage} alt={t.aboutIntroAlt} /> : null}
      </Grid>
      <Typography sx={{ marginBottom: spacing(4), lineHeight: 1.8, letterSpacing: '0.014em' }}>
        {t.aboutIntroDesc.split('\n').map((line, i) => (
          <React.Fragment key={i}>
            {i > 0 && <br />}
            {line}
          </React.Fragment>
        ))}
      </Typography>
      <MySocials tooltipPlacement="bottom" onClick={onClickExternalLink} />
    </Box>
  );
};

const ProfileImage = styled(GatsbyImage)(({ theme: { spacing, shape, breakpoints } }) => ({
  width: 200,
  height: 200,
  marginRight: spacing(2),
  borderRadius: shape.borderRadius,
  [breakpoints.down('lg')]: {
    display: 'none',
  },
}));

export default IntroSection;
