import React from 'react';
import { HeadProps, PageProps } from 'gatsby';
import AboutPage from 'containers/AboutPage';
import SEO from 'components/SEO';
import { SupportedLanguages } from 'models/languages';

const AboutTemplate = (_props: PageProps<object, { lang: SupportedLanguages }>) => {
  return <AboutPage />;
};

export default AboutTemplate;

export const Head = ({ pageContext, location }: HeadProps<object, { lang: SupportedLanguages }>) => {
  const alternateLanguages =
    pageContext.lang === 'ko' ? [{ lang: 'en', path: '/about/en/' }] : [{ lang: 'ko', path: '/about/' }];

  return (
    <SEO
      title=" | Evan Moon"
      lang={pageContext.lang}
      pathname={location.pathname}
      alternateLanguages={alternateLanguages}
      includeXDefaultAlternate={true}
    />
  );
};
