import { useStaticQuery, graphql } from 'gatsby';
import { useLanguage } from 'hooks/useLanguage';
import { useMemo } from 'react';

export const useRelatedProjectPosts = () => {
  const { language: lang } = useLanguage();
  const { allMarkdownRemark } = useStaticQuery<Queries.RelatedProjectPostsQuery>(graphql`
    query RelatedProjectPosts {
      allMarkdownRemark {
        group(field: { frontmatter: { relatedProject: SELECT } }) {
          fieldValue
          totalCount
          nodes {
            ...Post
          }
        }
      }
    }
  `);

  const group = useMemo(() => {
    return allMarkdownRemark.group.map(g => {
      return {
        ...g,
        nodes: g.nodes.filter(post => post.fields?.lang === lang),
      };
    });
  }, [allMarkdownRemark, lang]);

  return group;
};
